@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: #003;
  color: white;
  text-align: center;
}


.Cell {
  height: 150px;
  width: 150px;
  border-radius: 1px;
  background-color: #263238;
  transition: background-color 0.5s ease-in;
}

.Cell-lit {
  background-color: #01a4b9;
  box-shadow: inset 0 0 40px #00e1ff
}


@media only screen and (max-width: 1050px) {
  .Cell {
    height: 110px;
    width: 110px;
  }
  .Cell-lit {
    box-shadow: inset 0 0 35px #00e1ff
  }
}


@media only screen and (max-width: 900px) {
  .Cell {
    height: 90px;
    width: 90px;
  }
  .Cell-lit {
    box-shadow: inset 0 0 35px #00e1ff
  }
}

@media only screen and (max-width: 650px) {
  .Cell {
    height: 70px;
    width: 70px;
  }
  .Cell-lit {
    box-shadow: inset 0 0 30px #00e1ff
  }
}
.Board-title {
  width:90vw;
  font-size: 80px;
  font-family: Audiowide;
  letter-spacing: 5px;
  margin-bottom: 10px;
}

.Board-table {
  margin: 0 auto;
  border-radius: 13px;
  box-shadow: 0 0 40px #bbb, -5px 0 70px #f0f, 20px 0 80px #0ff,
   inset 0 0 50px #fff, 
   inset -90px 0 80px #f0f, 
   inset 20px 0 260px #0ff;
}


table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}
table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}
table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}
table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}


.neon-orange {
  color: #fb4264;
  text-shadow: 0 0 3vw #f40a35;
  text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
  0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
  color: #fed128;
}

.neon-blue {
  color: #426dfb;
  text-shadow: 0 0 3vw #2356ff;
  text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
  0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
  color: #28d7fe;
}

.win-msg {
  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%, -50%);
}

.neon-orange.dimmed {
  text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
  0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
  color: #806914;
}

.neon-blue.dimmed {
  text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
  0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
  color: #146c80;
}

.win-msg .neon-orange{
    animation: neon-orange 1.5s ease infinite;
  -moz-animation: neon-orange 1.5s ease infinite;
  -webkit-animation: neon-orange 1.5s ease infinite;
}

.win-msg .neon-blue{
    animation: neon-blue 1.5s ease infinite;
  -moz-animation: neon-blue 1.5s ease infinite;
  -webkit-animation: neon-blue 1.5s ease infinite;
}


@-webkit-keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}


@keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@-webkit-keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}
@media only screen and (max-width: 1050px) {
  .Board-title {
    width:95vw;
    font-size: 70px;
  }
}


@media only screen and (max-width: 900px) {
  .Board-title {
    width:95vw;
    font-size: 50px;
    margin-bottom: 5px;
  }
}

@media only screen and (max-width: 650px) {
  .Board-title {
    width:100vw;
    font-size: 40px;
    letter-spacing: 4px;
    margin-bottom: 5px;
  }
}
@media only screen and (max-height: 350px) {
  .win-msg {
    top: 60%;
  }
}



.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

